import React, {Component} from 'react';
import {connect} from 'react-redux';
import {GBP_RATE_ID} from '../../actions/const';
import {dispatchItem} from '../../actions/dispatchItem';

import GBPRateComponent from '../../components/Rate/GBPRate/GBPRate';

class GBPRate extends Component {

    componentDidMount() {
        this.props.onChangeValue(GBP_RATE_ID);
    }

    render() {
        return (
            (this.props.localState.status) ?
           (<React.Fragment>
               <GBPRateComponent value = {this.props.localState.value}/>
            </React.Fragment>)
            :
            (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        localState: state.item[GBP_RATE_ID]
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        onChangeValue: (id) => {
            dispatch(dispatchItem(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(GBPRate);