import React, {Component} from 'react';
import {connect} from 'react-redux';

import {dispatchItem} from '../../actions/dispatchItem';
import {MONERO_ID} from '../../actions/const';
import MONEROComponent from '../../components/Crypto/MONERO/MONERO';

class MONERO extends Component {

    componentDidMount() {
        this.props.onChangeValue(MONERO_ID);
    }

    render() {
        return (
            (this.props.localState.status) ?
           (<React.Fragment>
               <MONEROComponent value = {this.props.localState.value}/>
            </React.Fragment>)
            :
            (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        localState: state.item[MONERO_ID]
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        onChangeValue: (id) => {
            dispatch(dispatchItem(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(MONERO);