import React, {Component} from 'react';
import {connect} from 'react-redux';
import {AUD_RATE_ID} from '../../actions/const'
import {dispatchItem} from '../../actions/dispatchItem';

import AUDRateComponent from '../../components/Rate/AUDRate/AUDRate';

class AUDRate extends Component {

    componentDidMount() {
        this.props.onChangeValue(AUD_RATE_ID);
    }

    render() {
        return (
            (this.props.localState.status) ?
           (<React.Fragment>
               <AUDRateComponent value = {this.props.localState.value}/>
            </React.Fragment>)
            :
            (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        localState: state.item[AUD_RATE_ID]
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        onChangeValue: (id) => {
            dispatch(dispatchItem(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AUDRate);