import React, {Component} from 'react';
import {connect} from 'react-redux';

//Персональная константа
import {BTCCSH_ID} from '../../actions/const';
import {dispatchItem} from '../../actions/dispatchItem';

//Персональное имя ..Component
import BTCCSHComponent from '../../components/Crypto/BTCCSH/BTCCSH';

//! 
class BTCCSH extends Component {

    componentDidMount() {
        //!
        this.props.onChangeValue(BTCCSH_ID);
    }

    render() {
        return (
            (this.props.localState.status) ?
            //!
           (<React.Fragment>
               <BTCCSHComponent value = {this.props.localState.value}/>
            </React.Fragment>)
            :
            (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        //Константа
        localState: state.item[BTCCSH_ID]
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        onChangeValue: (id) => {
            dispatch(dispatchItem(id))
        }
    }
}

//!
export default connect(mapStateToProps, mapDispatchToProps)(BTCCSH);