import React, {Component} from 'react';
import {connect} from 'react-redux';
import {EOS_ID} from '../../actions/const';
import {dispatchItem} from '../../actions/dispatchItem';
import EOSComponent from '../../components/Crypto/EOS/EOS';

class EOS extends Component {

    componentDidMount() {
        this.props.onChangeValue(EOS_ID);
    }

    render() {
        return (
            (this.props.localState.status) ?
           (<React.Fragment>
               <EOSComponent value = {this.props.localState.value}/>
            </React.Fragment>)
            :
            (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        localState: state.item[EOS_ID]
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        onChangeValue: (id) => {
            dispatch(dispatchItem(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(EOS);