import React, {Component} from 'react';
import {connect} from 'react-redux';
import {STELLAR_ID} from '../../actions/const';
import {dispatchItem} from '../../actions/dispatchItem';
import STELLARComponent from '../../components/Crypto/STELLAR/STELLAR';

class STELLAR extends Component {

    componentDidMount() {
        this.props.onChangeValue(STELLAR_ID);
    }

    render() {
        return (
            (this.props.localState.status) ?
           (<React.Fragment>
               <STELLARComponent value = {this.props.localState.value}/>
            </React.Fragment>)
            :
            (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        localState: state.item[STELLAR_ID]
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        onChangeValue: (id) => {
            dispatch(dispatchItem(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(STELLAR);