import React, {Component} from 'react';
import Header from '../components/Header/Header';

import DollarRate from './Rate/DollarRate';
import EuroRate from './Rate/EuroRate';
import AUDRate from './Rate/AUDRate';
import CHFRate from './Rate/CHFRate';
import GBPRate from './Rate/GBPRate';
import JPYRate from './Rate/JPYRate';

import Bitcoin from './Crypto/Bitcoin';
import Ethereum from './Crypto/Ethereum';
import XRP from './Crypto/XRP';
import BTCCSH from './Crypto/BTCCSH';
import EOS from './Crypto/EOS';
import LTC from './Crypto/LTC';
import MONERO from './Crypto/MONERO';
import STELLAR from './Crypto/STELLAR';
import TETHER from './Crypto/TETHER';
import TRON from './Crypto/TRON';


import AddCity from './Weather/AddCity/AddCity';
import ShowWeather from './Weather/ShowWeather/ShowWeather';
import ToggleWeather from './Weather/ToggleWeather/ToggleWeather';
import Settings from './Settings/Settings';
import { EHOSTUNREACH } from 'constants';

class Wrapper extends Component {
    render() {
        return (
            <React.Fragment>
                <Header/>
                    <div id="main-content">
                        <section id = "rate">
                            <div className="container">
                                <h2>Курсы валют</h2>
                                <div className="row">
                                    <DollarRate/>
                                    <EuroRate/>
                                    <AUDRate/>
                                    <CHFRate/>
                                    <GBPRate/>
                                    <JPYRate/>
                                </div>
                            </div>
                        </section>
                        
                        <section id = "crypto">
                            <div className="container">
                                <h2>Курс крипты</h2>
                                <div className="row">
                                    <Bitcoin/>
                                    <BTCCSH/>
                                    <EOS/>
                                    <Ethereum/>
                                    <LTC/>
                                    <MONERO/>
                                    <STELLAR/>
                                    <TETHER/>
                                    <TRON/>
                                    <XRP/>
                                </div>
                            </div>
                        </section>

                        <section id = "weather">
                            <div className="container">
                                <h2>Прогноз погоды</h2>
                                <div className="row">
                                    <AddCity/>
                                </div>
                                <div className="row">
                                    <ShowWeather/>
                                </div>
                                <div className="row">
                                    <ToggleWeather/>
                                </div>
                            </div>
                        </section>
                    </div>
                <Settings/>
            </React.Fragment>
        ) 
    }
}

export default Wrapper;