import React, {Component} from 'react';


const Welcome = () => {
    return(
        <section id = "welcome" className="flex-center-center">
                <div>
                    <h2 className = "white">
                        Welcome
                    </h2>
                    <button id = "welcome__reload">
                        Accept
                    </button>
                </div>
        </section>
    )
}

export default Welcome