import React, {Component} from 'react';
import {connect} from 'react-redux';
import {LTC_ID} from '../../actions/const';
import {dispatchItem} from '../../actions/dispatchItem';
import LTCComponent from '../../components/Crypto/LTC/LTC';

class LTC extends Component {

    componentDidMount() {
        this.props.onChangeValue(LTC_ID);
    }

    render() {
        return (
            (this.props.localState.status) ?
           (<React.Fragment>
               <LTCComponent value = {this.props.localState.value}/>
            </React.Fragment>)
            :
            (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        localState: state.item[LTC_ID]
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        onChangeValue: (id) => {
            dispatch(dispatchItem(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LTC);