import React, {Component} from 'react';
import logo from './img/logo.svg'

class Header extends Component {
    render() {
        return(
            <React.Fragment>
                <section id = "header">
                    <div className="container">
                        <div className="flex-center-row">
                            <div>
                                <h1>
                                    All-in-One
                                </h1>
                            </div>
                            <div>
                                 <img src = {logo} alt="" id = "header__img"/>
                            </div>
                        </div>
                    </div>
                </section>
            </React.Fragment>
        )
    }
}

export default Header;